import { useState } from "react";
import { useAuth } from "../../context/AuthContext";
import Navbar from "./Navbar";
import Footer from "./Footer";

const MainLayout = ({ children, showFooter = true, onNavigate }) => {
  const { user } = useAuth();

  const handleDetailClick = (content, type = 'movie') => {
    // Demo saja - tampilkan alert
    alert(`More info about: ${content.title}`);
  };

  const handlePlayClick = (content, episode = null) => {
    // Demo saja - tampilkan alert
    alert(`Playing: ${content.title}`);
  };

  const handleUpgradeClick = () => {
    // Demo saja - tampilkan alert
    alert('Upgrade to Premium (demo)');
  };

  return (
    <div className="min-h-screen bg-background font-lato">
      <Navbar onNavigate={onNavigate} />

      <main className="pt-16 sm:pt-20 md:pt-[94px]">
        {children({
          user,
          onDetailClick: handleDetailClick,
          onPlayClick: handlePlayClick,
          onUpgradeClick: handleUpgradeClick
        })}
      </main>

      {showFooter && <Footer />}
    </div>
  );
};

export default MainLayout;